import React, { useState, useEffect } from "react";
import MaterialTable from "material-table";
import { makeStyles, Link, Button } from "@material-ui/core/";

const useStyles = makeStyles((theme) => ({
  root: {
    padding: "30px 20px",
  },
  button: {
    background: "#ff2262",
    color: "white",
  },
}));

const Table = ({ admin }) => {
  const classes = useStyles();
  const [state, setState] = useState({
    columns: [
      { title: "№", field: "item", type: "numeric"  },
      { title: "Вид работ", field: "typeWork" },
      { title: "Объект", field: "object" },
      { title: "Начало работ", field: "beginningOfWork", type: "date" },
      { title: "Окончание работ", field: "completionOfWork", type: "date" },
      { title: "Начало тендера", field: "beginningOfTenders", type: "date" },
      {
        title: "Окончание тендера",
        field: "completionOfTenders",
        type: "date",
      },
      { title: "Стоимость работ", field: "costOfWork", type: "numeric" },
      {
        title: "Ссылка на документы",
        field: "link",
        render: (rowData) => (
          <Button
            // className={classes.button}

            variant="outlined"
            color="secondary"
            href={rowData.link}
            target="_blank"
            rel="noreferrer"
          >
            Скачать
          </Button>
        ),
      },
    ],
    data: [
      {
        item: "1",
        typeWork: "Отделочные работы",
        object: "Солнечный город Супер ЖД 1 корп 4",
        beginningOfWork: "20.09.2020",
        completionOfWork: "30.09.2020",
        beginningOfTenders: "10.10.2020",
        completionOfTenders: "25.10.2020",
        costOfWork: 630000,
        link: "https://vk.com/kirill_1131",
      },
      {
        item: "2",
        typeWork: "Монолитные работы",
        object: "Светлая Долина ЖД 1-15 корп 2",
        beginningOfWork: "20.09.2020",
        completionOfWork: "30.09.2020",
        beginningOfTenders: "10.10.2020",
        completionOfTenders: "25.10.2020",
        costOfWork: 990000,
        link: "https://vk.com/andreevth",
      },
    ],
    localization: {
      toolbar: {
        searchTooltip: "Поиск",
        searchPlaceholder: "Поиск",
        nRowsSelected: "{0} элементов(s)",
      },
      pagination: {
        labelDisplayedRows: "{from}-{to} до {count}",
        labelRowsSelect: "элементов",
        labelRowsPerPage: "элементов на странице:",
        firstAriaLabel: "Первая страница",
        firstTooltip: "Первая страница",
        previousAriaLabel: "Предыдущая страница",
        previousTooltip: "Предыдущая страница",
        nextAriaLabel: "Следующая страница",
        nextTooltip: "Следующая страница",
        lastAriaLabel: "Предыдущая страница",
        lastTooltip: "Последняя страница",
      },
    },
    options: {
      pageSizeOptions: [5,10,15,20,25,30]
    }
  });

  return (
    <div className={classes.root}>
      <MaterialTable
        title=""
        columns={state.columns}
        data={state.data}
        localization={state.localization}
        options={state.options}
        editable={
          admin      
            ? {
                onRowAdd: (newData) =>
                  new Promise((resolve) => {
                    setTimeout(() => {
                      resolve();
                      setState((prevState) => {
                        const data = [...prevState.data];
                        data.push(newData);
                        return { ...prevState, data };
                      });
                    }, 600);
                  }),
                onRowUpdate: (newData, oldData) =>
                  new Promise((resolve) => {
                    setTimeout(() => {
                      resolve();
                      if (oldData) {
                        setState((prevState) => {
                          const data = [...prevState.data];
                          data[data.indexOf(oldData)] = newData;
                          return { ...prevState, data };
                        });
                      }
                    }, 600);
                  }),
                onRowDelete: (oldData) =>
                  new Promise((resolve) => {
                    setTimeout(() => {
                      resolve();
                      setState((prevState) => {
                        const data = [...prevState.data];
                        data.splice(data.indexOf(oldData), 1);
                        return { ...prevState, data };
                      });
                    }, 600);
                  }),
              }
            : null
        }
      />
    </div>
  );
};
export { Table };
