import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import {
  makeStyles,
  Drawer,
  AppBar,
  Toolbar,
  Typography,
  IconButton,
  MenuItem,
  Menu,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Container,
  Button,
} from "@material-ui/core/";

import InboxIcon from "@material-ui/icons/MoveToInbox";
import MailIcon from "@material-ui/icons/Mail";
import MenuIcon from "@material-ui/icons/Menu";
import AccountCircleIcon from "@material-ui/icons/AccountCircle";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    // background: "#ff2262"
  },
  menuButton: {
    marginRight: theme.spacing(2),
    // background: "#ff2262",
  },
  title: {
    flexGrow: 1,
  },
  menuAppBar: {
    background: "#5e00c6",
  },
}));

const MenuApp = ({ admin }) => {
  const classes = useStyles();
  const [auth, setAuth] = useState(true);
  const [anchorEl, setAnchorEl] = useState(null);
  const open = Boolean(anchorEl);

  // HiddenPanel
  const listData = ["Inbox", "Starred", "Send email", "Drafts"];
  const [toggle, setToggle] = useState(false);

  const renderList = listData.map((text, index) => (
    <ListItem button key={text}>
      <ListItemIcon>
        {index % 2 === 0 ? <InboxIcon /> : <MailIcon />}
      </ListItemIcon>
      <ListItemText primary={text} />
    </ListItem>
  ));

  const handleChange = (event) => {
    setAuth(event.target.checked);
  };

  const handleMenu = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <div className={classes.root}>
      <AppBar className={classes.menuAppBar} position="static">
        <Toolbar>
          <IconButton
            edge="start"
            className={classes.menuButton}
            color="inherit"
            aria-label="menu"
            onClick={() => setToggle(true)}
          >
            <MenuIcon />
          </IconButton>
          <Typography variant="h6" className={classes.title}>
            <Link
              style={{
                textDecoration: "none",
                color: "white",
              }}
              to="/"
            >
              AK BARS TENDERS
            </Link>
          </Typography>
          {auth && (
            <div>
              <IconButton
                aria-label="account of current user"
                aria-controls="menu-appbar"
                aria-haspopup="true"
                onClick={handleMenu}
                color="inherit"
              >
                <AccountCircleIcon />
              </IconButton>
              <Menu
                id="menu-appbar"
                anchorEl={anchorEl}
                anchorOrigin={{
                  vertical: "top",
                  horizontal: "right",
                }}
                keepMounted
                transformOrigin={{
                  vertical: "top",
                  horizontal: "right",
                }}
                open={open}
                onClose={handleClose}
              >
                <Link
                  style={{
                    textDecoration: "none",
                    color: "#000000DE",
                  }}
                  to="/sign-in"
                >
                  <MenuItem onClick={handleClose}>Войти</MenuItem>
                </Link>

                <Link
                  style={{
                    textDecoration: "none",
                    color: "#000000DE",
                  }}
                  to="/sign-up"
                >
                  <MenuItem onClick={handleClose}>Регистрация</MenuItem>
                </Link>
              </Menu>
            </div>
          )}
          <Drawer
            anchor={"right"}
            open={toggle}
            onClose={() => setToggle(false)}
          >
            <List>{renderList}</List>
          </Drawer>
        </Toolbar>
      </AppBar>
    </div>
  );
};
export { MenuApp };
